//CHECKBOX TOOTIP
.checkbox-tooltip {
	display: inline-block;
	position: fixed;
	z-index: 30;
	padding: 0 8px;
	height: 48px;
	line-height: 46px;
	text-align: center;
	background-color: #FFFFFF;
	box-shadow: 0px 4px 10px #D5D7DD;
	border-radius: 3px;
	left: 10%;
	top: 10%;
}
.checkbox-tooltip-btn {
	padding: 8px 16px;
	display: inline;
	font-weight: 600;
	font-size: 12px;
	color: #FFFFFF;
	background: linear-gradient(79.5deg, #C83947 0%, #D25050 99.46%);
	border-radius: 3px;
}

.checkbox-btn {
	display: none;
	@media (max-width:$md3+px){
		display: inline-block;
		position: fixed;
		z-index: 30;
		width: 100%;
		bottom: 0;
		line-height: 86px;
		text-align: center;
		height: 86px;
		background: #FFFFFF;
		box-shadow: 0px -4px 10px #D5D7DD;
		border-radius: 3px;
	}
}
.checkbox-btn-btn {
	padding: 15px 105px;
	display: inline;
	font-weight: 600;
	font-size: 16px;
	color: #FFFFFF;
	background: linear-gradient(79.5deg, #C83947 0%, #D25050 99.46%);
	border-radius: 3px;
}


//TITLE
.title {
	font-size: 16px;
	line-height: 1.4;
	color: #000;
	font-weight: 600;
	display: inline-block;
	margin: 0px 8px 16px 0px;
}

/* SPOILER */
.spoiler-content {
	font-size: 14px;
	padding: 10px 0px 10px 0px;
	&:hover {
		background-color: #F4F5F7;
	}
}
.spoiler {
	@include flexbox();
	justify-content: space-between;
	&_s {
		justify-content: flex-start;
		div {
			&:last-child {
				div {
					left: -55px;
					// background-color: red;
				}
			}
		}
	}
	&:hover {
		cursor: pointer;
		background-color: #F4F5F7;
	}
	div {
		&:last-child {
			margin: 0px 20px 0px 10px;
			div {
				top: 35px;
				left: -55px;
				@media (max-width:$md1+px){
					top: 45px;
					left: -250px;
				}
				@media (max-width:$md3+px){
					left: -400px;
				}
				@media (max-width:$md4+px){
					left: -300px;
				}
				@media (max-width:370px){
					left: -250px;
				}
			}
		}
	}
	font-size: 12px;
	position: relative;
	border-bottom: 1px solid #D5D7DD;
	padding: 16px 0 16px 34px;
	&:after {
		content: "";
		display: inline-block; 
		position: absolute; 
		left: 15px; 
		padding: 4px; 
		border: solid #D5D7DD;
		border-width: 0px 1px 1px 0; 
		height: 0; 
		width: 0;
		@include anim(0.3s,0s);
		top: 14px;
		@include rotate(45deg);
	}
}
.spoiler.active {
	&:after {
		top: 22px; 
		@include rotate(-135deg);
	}
}
.spoiler-content {
	display: none;
}
.spoiler-content.active {
	display: block;
}
