}
/* RADIO BTN */
.radio-btn {
	font-size: 12px;
	padding: 15px 0px 15px 58px;
	position: relative;
	&:after {
		content: "";
		display: inline-block; 
		position: absolute; 
		top: 11.5px; 
		left: 34px; 
		height: 16px; 
		width: 16px; 
		border: 1px solid #94979E;
		border-radius: 50%;
	}
	&:before {
		content: "";
		display: inline-block; 
		position: absolute; 
		top: 16px; 
		left: 38px; 
		height: 8px; 
		width: 8px; 
		background-color: transparent;
		border-radius: 50%;
		@include anim(0.3s,0s);
	}
	&:hover {
		cursor: pointer;
		&:after {
			border-color: #C83947;
		}
	}
	span {
		color: #61646B;
	}
	input {
		display: none;
	}
}
.radio-btn.active {

	&:before {
		background-color: #C83947;;
	}
}
