/* ОСНОВЫНЫЕ СТИЛИ ДЛЯ ГЛАВНОй СТРАНИЦЫ */

/* FITLER */


/* CATEGORY */
.filter {
	@media (max-width:$md3+px){
		display: none;
	}
	&__body {
		max-width: 235px;
		margin: 50px 0px 0px 0px;
	}
}
.filter-category-list {
	&_one {
		span {
			display: inline-block;
			padding: 0px 0px 0px 15px;
			position: relative;
			margin: 0px 0px 8px 0px;
			font-size: 12px;
			&:hover {
				cursor: pointer;
			}
			&:after {
				content: "";
				display: inline-block; 
				position: absolute; 
				top: 0px; 
				left: 0; 
				padding: 3px; 
				border: solid #94979E;
				border-width: 0px 1px 1px 0; 
				height: 0; 
				width: 0;
				@include rotate(45deg);
				@include anim(0.3s,0s);
			}
		}
	}
	&_one.active {
		.one:after {
			@include rotate(135deg);
		}
	}
	&_two {
		li {
			display: none;
		}
		li.show {
			display: block;
		}
		span {
			padding: 0px 0px 0px 25px;
			&:after {
				left: 10px;
			}
			&:hover {
				cursor: pointer;
			}
		}
	}
	&_three {
		padding: 0px 0px 0px 35px;
		li {
			display: none;
			input {
				display: none;
			}
		}
		li.show {
			display: block;
		}
		
		li.active span {
			background-color: rgba(213, 215, 221, 0.5);
		}
		span {
			@include anim(0.3s,0s);
			width: 100%;
			padding: 4px 2px 4px 6px;
			margin: 0;
			border-radius: 2px;
			&:after {
				display: none;
			}
			&:hover {
				background-color: rgba(213, 215, 221, 0.3);
				cursor: pointer;
			}
		}
	}
}
span.active_arrow {
	&:after {
		@include rotate(135deg);
	}
}

