/* FILTER ADAPTIVE*/

.filter-adaptive {
	@include anim(0.3s,0s);
	position: absolute;
	width: 100%;
	height: 100%;
	overflow: auto;
	left: -100%;
	top: 0;
	background-color: #fff;
	z-index: 30;
}
.filter-adaptive-header {
	align-items: center;
	@include flexbox();
	justify-content: space-between;
	padding: 16px 25px;
	background: #FFFFFF;
	box-shadow: 0px 0px 10px #D5D7DD;
	margin: 0px 0px 10px 0px;
	&__backbtn {
		font-size: 12px;
		position: relative;
		&:after {
			content: "";
			display: inline-block; 
			position: absolute; 
			top: 2px; 
			left: -10px; 
			padding: 3px; 
			border: solid #000; 
			border-width: 0px 1px 1px 0; 
			height: 0; 
			width: 0;
			@include rotate(135deg);
		}
	}
	&__title {
		font-weight: 600;
		font-size: 24px;
		color: #000000;
	}
	&__clearbtn {
		font-size: 12px;
		color: #C83947;
	}
}
.filter-adaptive-content {
	height: 100%;
	&__spoiler {
		@include flexbox();
		justify-content: space-between;
		flex-wrap: wrap;
		color: #000000;
		font-size: 16px;
		padding: 17px 0px 17px 25px;
		border-bottom: 1px solid #D5D7DD;
		position: relative;
		&:after {
			content: "";
			display: inline-block; 
			position: absolute; 
			top: 22px; 
			left: 10px; 
			padding: 3px; 
			border: solid #000; 
			border-width: 0px 1px 1px 0; 
			height: 0; 
			width: 0;
			@include rotate(135deg);
		}
		&:nth-last-of-type(2) {
			border-width: 0;
		}
	}
	&__content {}
}

.filter-adaptive-content {
	overflow: auto;
	&__content {
		@include anim(0.3s,0s);
		position: absolute;
		background-color: #fff;
		width: 100%;
		height: 100%;
		left: -150%;
		top: 0;
		z-index: 30;
	}
}
.filter-adaptive-content-header {
	margin: 0px 0px 30px 0px;
	align-items: center;
	@include flexbox();
	justify-content: space-between;
	padding: 16px 25px;
	background: #FFFFFF;
	box-shadow: 0px 0px 10px #D5D7DD;
	&__backbtn {
		font-size: 12px;
		position: relative;
		&:after {
			content: "";
			display: inline-block; 
			position: absolute; 
			top: 2px; 
			left: -10px; 
			padding: 3px; 
			border: solid #000; 
			border-width: 0px 1px 1px 0; 
			height: 0; 
			width: 0;
			@include rotate(135deg);
		}
	}
	&__title {
		font-weight: 600;
		font-size: 24px;
		color: #000000;
	}
}
.adaptivefilter__btn {
	display: none;
	@media (max-width:$md3+px){
